___CSS_LOADER_EXPORT___.push([module.id, `.purchase-h2-btn {
    padding: 40px 20px;
    font-size: 22px;
    color: var(--dark-blue);
}

.purchase_list {
    margin-bottom: 60px;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.9s ease, transform 0.9s ease;
    color: var(--dark-blue);
}

.purchase_list.visible {
    opacity: 1;
    transform: translateY(0);
}

.subtitle {
    padding-bottom: 20px;
    font-size: 26px;
}

.purchase_list_text {
    font-size: 20px;
    color: var(--dark-blue);
}

.page_text_bottom {
    padding: 20PX 0 40px 0;
    font-size: 22px;
    color: var(--dark-blue);
    text-align: center;
    line-height: 2;
}

.bottom_button {
    position: relative;
    min-height: 50px;
    width: 100%;
    background-color: var(--green);
    border-radius: var(--border-radius);
    transition: all 0.9s;
    margin-top: 40px;
